.overall-progress {
  width: 33%;
  min-width: 250px;
}

.overall-progress-bar {
  height: 24px;
  border: 1px solid #D6DDEB;
  border-radius: 4px;
  box-sizing: border-box;
  padding: 1px;
  background-color: #FFFFFF;
}
.overall-progress-bar .overall-progress-text {
  text-align: center;
  margin-bottom: 4px;
  color: #212C4C;
}
.overall-progress-bar .overall-progress-text::after {
  content: "";
  color: #324371;
}
.overall-progress-bar table {
  position: relative;
}
.overall-progress-bar td:first-child:not(.mrProgressText) {
  background-color: #D6DDEB;
  border-radius: 4px;
}
.overall-progress-bar .mrProgressText {
  display: block;
  position: absolute;
  top: -1px;
  left: -42px;
}

html[dir=rtl] .mrProgressText {
  left: 4px;
}

.overall-progress.complex {
  width: auto;
  min-width: 0;
  position: relative;
}
.overall-progress.complex div {
  display: inline-block;
  margin-right: 0.5em;
  border-radius: 4px;
}
.overall-progress.complex div.m-progress-section {
  min-width: 24px;
  height: 24px;
}
.overall-progress.complex div.m-progress-section.no-progress {
  background: #324371;
}
.overall-progress.complex div.m-progress-section.in-progress {
  background: repeating-linear-gradient(45deg, #324371, #324371 4px, #455B97 4px, #455B97 8px);
}
.overall-progress.complex div.m-progress-section.complete {
  background: #324371;
}
.overall-progress.complex div.m-progress-section div.a-progress-page {
  display: none;
}
.overall-progress.complex div.m-progress-section.current-section {
  min-width: 12px;
  background: transparent;
}
.overall-progress.complex div.m-progress-section.current-section div.a-progress-page {
  display: inline-block;
}
.overall-progress.complex div.a-progress-page {
  width: 12px;
  height: 32px;
  position: relative;
  top: 4px;
  background: #324371;
}
.overall-progress.complex div.a-progress-page.current-page {
  outline: 1px solid #324371;
  outline-offset: 4px;
  margin: 0 0.75em 0 0.25em;
}
.overall-progress.complex div.a-progress-page.current-page:last-child {
  margin-right: 0.25em;
}
.overall-progress.complex div.a-progress-page.current-page,
.overall-progress.complex div.a-progress-page.current-page ~ div.a-progress-page {
  background: #324371;
}
.overall-progress.complex div:last-child {
  margin-right: 0;
}

html[dir=rtl] .overall-progress.complex div {
  margin-right: 0;
  margin-left: 0.5em;
}
html[dir=rtl] .overall-progress.complex div.a-progress-page.current-page {
  margin: 0 0.25em 0 0.75em;
}
html[dir=rtl] .overall-progress.complex div.a-progress-page.current-page:last-child {
  margin-left: 0.25em;
}
html[dir=rtl] .overall-progress.complex div:last-child {
  margin-left: 0;
}

#nav-controls {
  display: flex;
  justify-content: space-between;
  flex-direction: row-reverse;
  flex-wrap: wrap;
}
#nav-controls input {
  box-shadow: none !important;
}

#prevstop {
  display: flex;
  flex-grow: 1;
  margin-right: 1em;
}

html[dir=rtl] #prevstop {
  margin-right: auto;
  margin-left: 1em;
}

#nextonly {
  display: block;
  text-align: right;
}

#nav-controls > .a-button-next {
  order: 1;
}

#nav-controls > .a-button-prev {
  order: 3;
}

#nav-controls > .a-button-stop {
  order: 2;
}

.page-links {
  margin-top: 1em;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.page-links a {
  color: #212C4C;
  display: inline-block;
  margin-right: 1em;
}

html[dir=rtl] .page-links a {
  margin-right: 0;
  margin-left: 1em;
}

.bold {
  font-weight: 700;
}

.ltBold {
  font-weight: 600;
}

.caps {
  text-transform: uppercase;
}

.italic {
  font-style: italic;
}

.underline {
  text-decoration: underline;
}

.minustwo {
  font-size: calc(16px - 2pt);
}

.plustwo {
  font-size: calc(16px + 2pt);
}

.minusfour {
  font-size: calc(16px - 4pt);
}

.plusfour {
  font-size: calc(16px + 4pt);
}

.sub {
  vertical-align: sub;
}

.sup,
.super {
  vertical-align: super;
}

input[type=button],
input[type=submit],
input[type=reset],
button[class^=a-button] {
  display: block;
  box-sizing: content-box;
  min-width: 3em;
  min-height: 30px;
  -webkit-appearance: none;
  margin: 0 0 12px 0;
  border: 1px solid #455B97;
  border-radius: 4px;
  padding: 4px 1em;
  background-color: #455B97;
  color: #FAFAFA;
  white-space: normal;
}

input[type=button]:hover,
input[type=submit]:hover,
input[type=reset]:hover,
button:hover {
  background-color: #324371 !important;
  color: #FAFAFA;
}

input[type=button]:active,
input[type=submit]:active,
input[type=reset]:active {
  background-color: #FAFAFA !important;
  color: #455B97;
}

button:disabled,
input[type=button]:disabled,
input[type=submit]:disabled,
input[type=reset]:disabled {
  border-color: #449B9B !important;
  background-color: #449B9B !important;
  color: #FAFAFA !important;
}

button[data-checked=true],
input[type=button][data-checked=true],
input[type=submit][data-checked=true] {
  background-image: url("/Survey/images/formal/checkmark-blue.png");
  background-repeat: no-repeat;
  background-position: center;
}
button[data-checked=true]:active,
input[type=button][data-checked=true]:active,
input[type=submit][data-checked=true]:active {
  background-image: url("/Survey/images/formal/checkmark-blue.png");
}
button[data-checked=true]:disabled,
input[type=button][data-checked=true]:disabled,
input[type=submit][data-checked=true]:disabled {
  background-image: url("/Survey/images/formal/checkmark-blue.png");
}

button.a-button-preterminator,
button.a-button-postterminator {
  position: relative;
  border: none;
  background: transparent !important;
  top: 0.5em;
  width: 2em;
  min-width: 2em;
  padding: 0 0.5em;
  margin: 0;
  display: none;
  color: #455B97;
}

.has-terminators .a-button-preterminator,
.has-terminators .a-button-postterminator {
  display: block;
}

button.a-button-postterminator {
  margin-left: 1em;
}

html[dir=rtl] button.a-button-postterminator {
  margin-left: 0;
  margin-right: 1em;
}

button.a-button-preterminator {
  margin-right: 1em;
}

html[dir=rtl] button.a-button-preterminator {
  margin-right: 0;
  margin-left: 1em;
}

.balance .a-button-option {
  flex: 0 0 auto;
  margin-right: 1em;
}

html[dir=rtl] .balance .a-button-option {
  margin-right: 0;
  margin-left: 1em;
}

.focused > .a-button-list-dropdown {
  background-image: url("/Survey/images/formal/white-chevron-up.png");
}

.m-input-button-wrapper {
  position: relative;
  display: inline-block;
}
.m-input-button-wrapper .m-button-spinner {
  display: none;
  position: absolute;
  right: calc(0.5em + 4px);
  top: 50%;
  transform: translateY(-50%);
}
.m-input-button-wrapper .m-button-spinner button {
  margin: 0;
  border: 0;
  border-radius: 0;
  min-width: 0;
  min-height: 0;
  background: #ECEFF5;
  color: #6B5B72;
  font-size: 12px;
  line-height: 1;
  width: 12px;
}
.m-input-button-wrapper .m-button-spinner button.a-button-spinner-up {
  padding: 2px 0 0 0;
}
.m-input-button-wrapper .m-button-spinner button.a-button-spinner-down {
  padding: 0 0 2px 0;
}
.m-input-button-wrapper .m-button-spinner button:hover, .m-input-button-wrapper .m-button-spinner button:focus {
  background: #D6DDEB !important;
}

html[dir=rtl] .m-input-button-wrapper .m-button-spinner {
  right: auto;
  left: calc(0.5em + 4px);
}

.show-spinner .m-button-spinner {
  display: block;
}

.read-only .m-button-spinner {
  display: none;
}

/* buttons where used as part of
 * the page navigation controls */
#nav-controls input {
  display: inline-block !important;
}
#nav-controls input[type=submit] {
  margin: 0;
