}
#nav-controls #prevstop input[type=submit] {
  margin-right: 1em;
}
#nav-controls #prevstop input[type=submit]:last-child {
  margin-right: 0;
}

html[dir=rtl] #nav-controls #prevstop input[type=submit] {
  margin-right: 0;
  margin-left: 1em;
}
html[dir=rtl] #nav-controls #prevstop input[type=submit]:last-child {
  margin-left: 0;
