.a-icon-multistate {
  display: inline-block;
  width: 18px;
  height: 17px;
  background-position-y: -28px;
}

.m-option-base .a-icon-multistate {
  position: absolute;
  top: 4px;
  left: 0;
}

html[dir=rtl] .m-option-base .a-icon-multistate {
  left: auto;
  right: 0;
}

.a-icon-multistate[data-icontype=single] {
  left: 1px;
}

html[dir=rtl] .a-icon-multistate[data-icontype=single] {
  left: auto;
  right: 1px;
}

/* Grid layout */
.m-structure-cell .m-option-base .a-icon-multistate {
  position: relative;
  top: 0.2em;
}

input[type=checkbox] + label > .a-icon-multistate {
  background-image: url("/Survey/images/formal/checkboxes.png");
  background-position-x: -42px;
}

input[type=radio] + label > .a-icon-multistate {
  background-image: url("/Survey/images/formal/radiobuttons.png");
  background-position-x: -42px;
}

input[type=checkbox]:checked + label > .a-icon-multistate,
input[type=radio]:checked + label > .a-icon-multistate {
  background-position-x: -14px;
}

.focus-control input[type=checkbox]:focus + label,
.focus-control input[type=radio]:focus + label,
.focus-control input[type=radio]:focus-visible + label,
.focus-control input[type=radio]:focus-within + label,
.focus-control input[type=checkbox]:focus-visible + label,
.focus-control input[type=checkbox]:focus-within + label {
  box-shadow: 0 11px 11px -6px rgba(0, 0, 0, 0.25), 0 -11px 11px -6px rgba(49, 46, 46, 0.25);
}

.m-list-optionlist input[type=checkbox]:focus + label,
.m-list-optionlist input[type=radio]:focus + label,
.m-list-optionlist input[type=radio]:focus-visible + label,
.m-list-optionlist input[type=radio]:focus-within + label,
.m-list-optionlist input[type=checkbox]:focus-visible + label,
.m-list-optionlist input[type=checkbox]:focus-within + label,
.m-select-combobox-unorderedlist input[type=checkbox]:focus + label,
.m-select-combobox-unorderedlist input[type=radio]:focus + label,
.m-select-combobox-unorderedlist input[type=radio]:focus-visible + label,
.m-select-combobox-unorderedlist input[type=radio]:focus-within + label,
.m-select-combobox-unorderedlist input[type=checkbox]:focus-visible + label,
.m-select-combobox-unorderedlist input[type=checkbox]:focus-within + label {
  box-shadow: none;
}

input[type=checkbox]:disabled + label > .a-icon-multistate,
input[type=radio]:disabled + label > .a-icon-multistate,
div[data-readonly=true] input[type=checkbox] + label > .a-icon-multistate,
div[data-readonly=true] input[type=radio] + label > .a-icon-multistate {
  background-position-x: -98px;
}

input[type=checkbox]:disabled:checked + label > .a-icon-multistate,
input[type=radio]:disabled:checked + label > .a-icon-multistate,
div[data-readonly=true] input[type=checkbox]:checked + label > .a-icon-multistate,
div[data-readonly=true] input[type=radio]:checked + label > .a-icon-multistate {
  background-position-x: -70px;
}

.m-list-optionlist:not(.display-icons) .a-icon-multistate {
  display: none;
}

.m-list-optionlist input:focus + label,
.m-list-optionlist input:focus-visible + label {
  outline: none;
}
.m-list-optionlist .a-icon-multistate {
  left: -2px;
}

html[dir=rtl] .m-list-optionlist .a-icon-multistate {
  left: auto;
  right: -2px;
}

textarea {
  width: 100%;
  background: #FFFFFF;
  border: 1px solid #8797C8;
  box-sizing: border-box;
  border-radius: 2px;
  padding: 0.5em;
  min-height: 100px;
  color: #449B9B;
  overflow-y: auto;
}

textarea:active,
textarea:focus,
textarea:hover {
  outline: none;
}

textarea:focus {
  border-color: #455B97;
}

textarea:focus-visible,
textarea:focus-within {
  outline: none;
  border-color: #455B97;
}

.focus-control textarea:focus {
  box-shadow: 0 11px 11px -9px rgba(0, 0, 0, 0.25), 0 -11px 11px -9px rgba(49, 46, 46, 0.25);
}

/* a-input-singlelineedit
 * essential styling for the fundamental HTML input element
 */
.a-input-singlelineedit {
  padding: 10px 16px;
  border-radius: 2px;
  border: 1px solid #8797C8;
  color: #449B9B;
  margin: 0 0 0.5em 0;
  width: 197px;
  background-color: #FFFFFF;
  box-sizing: content-box;
}

.a-label-prelabel + .m-input-button-wrapper input:focus {
  border-left: none;
}

.balance .a-input-singlelineedit,
.balance .m-input-singlelineedit {
  margin-right: 1em;
}

html[dir=rtl] .balance .a-input-singlelineedit,
html[dir=rtl] .balance .m-input-singlelineedit {
  margin-right: auto;
  margin-left: 1em;
}

/* m-input-singlelineedit
 * the molecule gets added by JavaScript when the a-input-singlelineedit
 * includes a pre or post label and therefore requires an additional wrapper
 * overflow: hidden added to fix issue with display in IE10
 */
.m-input-singlelineedit {
  display: inline-block;
  /* prevent double drop shadow when input is within a molecule container */
  margin-bottom: 0.5em;
}
.m-input-singlelineedit.focused .a-input-singlelineedit {
  box-shadow: none !important;
}
.m-input-singlelineedit.position-below {
  display: table;
}
.m-input-singlelineedit.position-below .a-input-singlelineedit {
  margin-left: 0;
}
.m-input-singlelineedit .a-input-singlelineedit {
  margin-bottom: 0;
  margin-right: 0;
}

html[dir=rtl] .m-input-singlelineedit.position-below .a-input-singlelineedit {
  margin-right: 0;
}
html[dir=rtl] .m-input-singlelineedit .a-input-singlelineedit {
  margin-left: 0;
}

/* numerical single-line inputs
 * styles for the input element (that are built on top of the above defaults)
 * along with additional classes to manage the 'spinner' control that appears
 */
.a-input-singlelineedit[type=number] {
  text-align: right;
  width: 197px;
  padding-right: 16px;
}

.show-spinner .a-input-singlelineedit[type=number] {
  padding-right: 28px;
  width: 181px;
}

html[dir=rtl] .show-spinner .a-input-singlelineedit[type=number] {
  padding-right: 0;
  padding-left: 28px;
}

/* remove the default spinner as we are using a custom implementation */
.a-input-singlelineedit[type=number] {
  -moz-appearance: textfield;
}
.a-input-singlelineedit[type=number]::-webkit-inner-spin-button, .a-input-singlelineedit[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  opacity: 0;
  margin: 0;
}

/* remove the clear entry button from IE as it breaks right-aligned text */
.a-input-singlelineedit[type=number]::-ms-clear {
  display: none;
}

/* IE10 does not support :read-only selector */
.read-only .a-input-singlelineedit,
.a-input-singlelineedit.read-only {
  border-color: #D6DDEB !important;
  cursor: default;
}

input.read-only:focus-visible {
  outline: none;
}

/* This following rule shouldn't be necessary, as ready-only elements shouldn't get focus,
 * however I have observed Firefox adding an outline and this prevents that behaviour  */
input:read-only:focus-visible {
  outline: none;
}

input[readonly]:focus-visible {
  outline: none;
}

.m-input-singlelineedit:last-of-type,
.a-input-singlelineedit:last-of-type {
  margin-bottom: 0;
}

.a-input-singlelineedit:not([readonly]):focus {
  outline: none;
  border: 1px solid #455B97;
}

/* inputs when a child of a checkbox (other specifier) */
.m-option-base .a-input-singlelineedit {
  margin-left: 0.5em;
  margin-bottom: 0;
  padding: 0;
  border: 0;
  border-radius: 0;
  border-bottom: 1px solid;
  border-top: none;
  width: 151px;
  height: auto;
}
.m-option-base .a-input-singlelineedit:focus {
  border: none;
  border-bottom: 1px solid #455B97;
}

html[dir=rtl] .m-option-base .a-input-singlelineedit {
  margin-left: 0;
  margin-right: 0.5em;
}

/* remove leading border if there is a pre-label which has to account for
 * for the possibility of the presence of a button wrapper around the input
 */
.a-label-prelabel + .a-input-singlelineedit,
.a-label-prelabel + div > .a-input-singlelineedit {
  border-left: none;
  border-radius: 0 2px 2px 0;
}

html[dir=rtl] .a-label-prelabel + .a-input-singlelineedit,
html[dir=rtl] .a-label-prelabel + div > .a-input-singlelineedit {
  border-left: 1px solid;
  border-right: none;
}

*[class*=-label-] {
  cursor: default;
}
*[class*=-label-] * {
  cursor: default;
}

.m-list-optionlist label {
  position: relative;
  display: inline-block;
  height: 100%;
  width: 100%;
}

.a-label-details {
  position: absolute;
  width: 100%;
