.focused .grid-row-total .a-label-prelabel,
.focused .grid-row-total .a-label-postlabel,
.focused .grid-column-total .a-label-prelabel,
.focused .grid-column-total .a-label-postlabel,
.focused .grid-grandtotal .a-label-prelabel,
.focused .grid-grandtotal .a-label-postlabel {
