* {
  font-size: 16px;
  line-height: 19px;
}

html {
  height: 95vh;
}

body {
  background: #FAFAFA;
  color: #212C4C;
  padding: 0;
  margin: 0 2em;
  display: flex;
  flex-direction: column;
  font-size: 16px;
  line-height: 19px;
  font-weight: 400;
}

html[dir=rtl] body,
html[dir=rtl] input,
html[dir=rtl] textarea,
html[dir=rtl] select {
  font-family: "Rubik", "RubikLocal", serif;
}

body,
input,
textarea,
select {
  font-family: "Karla", "KarlaLocal", sans-serif;
}

body > form {
  min-height: 95vh;
  max-height: -webkit-fill-available;
  display: grid;
  grid-template-columns: 1fr minmax(343px, 1660px) 1fr;
  grid-template-rows: 130px auto 110px;
  grid-template-areas: ". header ." ". content ." ". footer .";
}

.header {
  grid-area: header;
  box-sizing: border-box;
  height: 130px;
  padding-bottom: 1em;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  border-bottom: 1px solid #8797C8;
}

.surroundcontent {
  grid-area: content;
  min-height: 66vh;
}

.footer {
  grid-area: footer;
  height: 110px;
  border-top: 1px solid #8797C8;
  padding: 1em 0;
}

.hiddencontrol {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  width: 1px;
  height: 1px;
}

[data-hidden=true] {
  visibility: hidden;
}

.unavailable {
  visibility: hidden;
}

.nowrap {
  white-space: nowrap;
}

.align-left {
  text-align: left !important;
}

.align-center {
  text-align: center !important;
}

.align-right {
  text-align: right !important;
}

@media screen and (max-width: 1023px) {
  body {
    margin: 0;
  }
  .header {
    height: 75px;
    padding: 0 1em 0.8em 1em;
  }
  .footer {
    padding: 1em;
  }
  body > form {
    grid-template-rows: 75px auto 110px;
  }
}
input:disabled {
  opacity: 1;
}

.focus-control input:focus {
  box-shadow: 0 11px 11px -9px rgba(0, 0, 0, 0.25), 0 -11px 11px -9px rgba(49, 46, 46, 0.25);
}

